.about__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;
}

.about__img {
    width: 350px;
    border-radius: 1.5rem;
    justify-self: center;
    z-index: 50;
}

.about__img__container {
    display: flex;
    justify-content: center;
    position: relative;
}

.box__background {
    position: absolute;
    width: 350px;
    height: 450px;
    background: var(--title-color);
    bottom: 2rem;
    right: 5rem;
    border-radius: 1.5rem 1.5rem 0 1.5rem;
    z-index: 1;
}

.about__img_box {
    margin: 0;
    padding: 0;
    width: 350px;
    z-index: 50;
}

.about__info {
    grid-template-columns: repeat(3, 140px);
    gap: 0.5rem;
    margin-bottom: var(--mb-2);
}

.about__box {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.75rem;
    text-align: center;
    padding: 1rem 1.5rem;
}

.about__icon {
    font-size: 1.5rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-5);
}

.about__title {
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
}

.about__subtitle {
    font-size: var(--tiny-font-size);
}

.about__description {
    padding: 0 4rem 0 0;
    margin-bottom: var(--mb-2-5);
}

/*=============== BREAKPOINTS ===============*/
/* For large screen devices */
@media screen and (max-width: 992px) {
    .about__container {
        grid-template-columns: 1fr;
        row-gap: 2.5rem;
    }

    .about__img {
        width: 220px;
    }

    .box__background {
        display: none;
    }

    .about__img_box {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .about__box {
        padding: 0.75rem 0.5rem;
    }
    .about__data {
        text-align: center;
    }
    .about__info {
        justify-content: center;
    }
    .about__description {
        padding: 0 5rem;

        margin-bottom: 2rem;
    }
}

/* For medium screen devices */

@media screen and (max-width: 576px) {
    body {
        margin: 0 0 var(--header-height) 0;
    }

    .about__info {
        grid-template-columns: repeat(3, 1fr);
    }

    .about__description {
        padding: 0;
    }
}

/* For small screen devices */
@media screen and (max-width: 350px) {
    .about__info {
        grid-template-columns: repeat(2, 1fr);
    }
    .section__subtitle {
        margin-bottom: var(--mb-3);
    }
}
